@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body, p {

  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body{
  margin: 0;
  padding: 0;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#sideBar {
  float:left;
  height: 100vh; 

}

.contentContainer {
  float:right;
  width:76vw;
  height:100vh;
}


#site-container {
  background: white;
  width: 100vw;
  height:100vh;
 
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.sidebar {
  width: 20vw;
  height: 100vh;
  background: #F2E9E4;
  padding-left: 2vw;
  padding-right: 2vw;
  display: flex;
  flex-direction: column;
  list-style: none;
  margin: 0;
  position: absolute;
  top: 0;
  bottom: 0;
}

.menuTitle {
  font-size: 350%;
  margin-top: 0;
  margin-bottom: 0.33em;
  cursor: pointer;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: bold;
  color: black;
}

a.menuTitle{
  text-decoration: none;
}

.menuBtn {
  background: #43415f;
  border-radius: 5px;
  height: 5.5%;
  width: 100%;
  opacity: 0;
  margin-bottom: 15px;
  text-align: center;
  transform-origin-y: 0%;
  color: white;
  cursor: pointer;
}
.menuBtn:nth-child(1) {
  background: #f69a9a;
}

.menuBtn:nth-child(2) {
  background: #83ae9b;
}

.menuBtn:nth-child(3) {
  background: #ef4566;
}

.menuBtn:nth-child(4) {
  background: #f9cdae;
}

.menuBtn:nth-child(5) {
  background: #c8c8a9;
}

.menuLink{
  margin: 0px; display: block; width: 100%; height: 100%;
  margin-top: 10px ;
  font-size: 100%;
  text-decoration: none;
  color: white;
  font-weight: bold;

  
}





